body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a{
  color: inherit;
  text-decoration: none
}

.video-container {
  position: relative;
  width: calc(100vh - 220px);
  overflow: hidden;
  background: #000;
  margin: 0 auto;
  max-width:100vw
}

.video-container:before {
  content: "";
  display: block;
  padding-top: 100%;
}

.video, .canva{
  /* width: 100vw; */
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  color: white;
  text-align: center;
}
.video{
  /* object-fit: cover */
}
.canva{
  object-fit: contain
}
.hidden {
  display: none !important;
  visibility: hidden;
}
.captureButton{
margin: 10px auto !important;
display: block !important;
}
.iconactive, .activeTextButton{
  color: #212985 !important;
}
